<template>
  <div id="delete" style="display: flex;align-items: center;flex-direction: column;height: 600px;justify-content: space-between">
    <div><button @click="logout" style="width: 200px;height: 100px;border-radius: 10px;">log out</button></div>
    <div><button @click="del" style="width: 200px;height: 100px;border-radius: 10px;">delete</button></div>
    <div><button @click="delStorage" style="width: 200px;height: 100px;border-radius: 10px;">clear cache</button></div>
  </div>
</template>

<script>
  import {request2} from "../../network/request";

  export default {
    name: "DeleteUser",
    methods: {
      del() {
        request2({
          url: this.$store.state.globalUrl + "cleanalluser",
          method: 'get'
          }).then(res => {
            console.log('successfully delete')
        }).catch(err => {
          console.log('fail to delete')
        })
      },
      delStorage() {
        sessionStorage.clear()
      },
      logout() {
        request2({
          url: this.$store.state.globalUrl + '/logout',
          params: {
            username: this.$store.state.globalUserName
          }
        }).then(res => {
          this.$router.replace('/home')
        }).catch(err => {
          alert("下线失败")
        })
      }
    }
  }
</script>

<style scoped>

</style>



